import React from "react";
import PropTypes from "prop-types";
import { Panel, PanelHeader } from "@vkontakte/vkui";
import { BackButton } from "../../components/BackButton";
import Div from "@vkontakte/vkui/dist/components/Div/Div";
import Button from "@vkontakte/vkui/dist/components/Button/Button";
import { ArrowIcon } from "../../icons/ArrowIcon";
import Input from "@vkontakte/vkui/dist/components/Input/Input";

import "./Questionnaire.css";

const questions = [
  {
    id: 1,
    text: "Вы курите?",
    options: ["Да", "Нет", "Иногда", "Когда пью"],
    input: null
  },
  {
    id: 2,
    text: "Вы пьете?",
    options: ["Да", "Нет", "Иногда", "По праздникам"],
    input: null
  },
  {
    id: 3,
    text: "Укажите пол",
    options: ["Женский", "Мужской", "Другой"],
    input: null
  },
  {
    id: 4,
    text: "Сколько Вам лет?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш возраст"
    }
  },
  {
    id: 5,
    text: "Сколько Вы весите?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш вес"
    }
  },
  {
    id: 6,
    text: "Какой у Вас рост?",
    options: null,
    input: {
      type: "number",
      placeholder: "Введите Ваш рост"
    }
  }
];

let answers = [];

export class Questionnaire extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      questions,
      questionNumber: 0,
      inputValue: "",
      percentage: 0
    };
  }

  handleChange = event => {
    const { value, name } = event.target;

    this.setState({ [name]: value });
  };

  getProperRiskValue = valueToSet => {
    if (valueToSet > 100) {
      return 100;
    } else if (valueToSet < 0) {
      return 0;
    } else {
      return valueToSet;
    }
  };

  calculatePercentage = () => {
    this.setState({ percentage: Math.floor(Math.random() * 100) });
  };

  render() {
    const { questions = [null], questionNumber = 0, percentage } = this.state;
    const currentQuestion = questions[questionNumber];
    const buttonTitle =
      questionNumber !== questions.length - 1 ? "Дальше" : "Закончить";
    const currentQuestionId = currentQuestion.id;

    const borderColor = `rgb(${percentage}%, ${100 - percentage}%, 65%)`;

    return (
      <Panel id={this.props.id}>
        <PanelHeader left={<BackButton onClick={this.props.go} to="home" />}>
          Старт
        </PanelHeader>
        <Div className="questionnaire">
          <Div className="questionnaire__diagram-wrapper">
            <Div
              className="questionnaire__diagram"
              style={{ borderColor }}
            >{`${percentage}%`}</Div>
          </Div>
          <Div className="questionnaire__question">{currentQuestion.text}</Div>
          {currentQuestion.options ? (
            <Div className="questionnaire__options">
              {currentQuestion.options.map((option, index) => {
                return (
                  <Div
                    className="questionnaire__option"
                    key={index}
                    onClick={e => {
                      answers[questionNumber] = {
                        id: currentQuestionId,
                        value: option
                      };

                      this.calculatePercentage();

                      const elements = document.getElementsByClassName(
                        "questionnaire__option-active"
                      );

                      elements.length &&
                        Array.from(elements).forEach(element => {
                          element.classList.remove(
                            "questionnaire__option-active"
                          );
                        });

                      if (
                        !e.currentTarget.classList.contains(
                          "questionnaire__option-active"
                        )
                      ) {
                        e.currentTarget.classList.add(
                          "questionnaire__option-active"
                        );
                      }
                      console.log(answers);
                    }}
                  >
                    <span className="questionnaire__option-text">{option}</span>
                  </Div>
                );
              })}
            </Div>
          ) : (
            <Div className="questionnaire__input">
              <Input
                type="number"
                placeholder={currentQuestion.input.placeholder}
                value={this.state.inputValue}
                alignment="center"
                onChange={event => {
                  const value = event.currentTarget.value;

                  answers[questionNumber] = {
                    id: currentQuestionId,
                    value: value
                  };

                  this.setState({ inputValue: value });
                  console.log(answers);
                }}
              />
            </Div>
          )}

          <Div className="questionnaire__control">
            <Button
              size="xl"
              level="2"
              onClick={event => {
                if (!answers[questionNumber]) {
                  return;
                }

                this.setState({ inputValue: "" });

                const elements = document.getElementsByClassName(
                  "questionnaire__option-active"
                );

                elements.length &&
                  Array.from(elements).forEach(element => {
                    element.classList.remove("questionnaire__option-active");
                  });

                if (questionNumber !== questions.length - 1) {
                  this.setState({ questionNumber: questionNumber + 1 });
                } else {
                  answers = [];
                  this.props.go(event);
                }
              }}
              data-to="home"
              className="questionnaire__control-button"
            >
              <div style={{ marginBottom: "10px" }}>{buttonTitle}</div>
              <ArrowIcon />
            </Button>
          </Div>
        </Div>
      </Panel>
    );
  }
}

Questionnaire.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired
};
